<template>
    <div :key="i" v-for="(business, i) in businesses" class="business">
        <div class="profile-pic">
            <img :src="'https://horizontes.herokuapp.com/images/logos/' + business.logo" :alt="'logotipo' + business.name">
        </div>
        <article>
            <h3>{{business.name}}</h3>                   
            <p class="subtitle"><i>"{{business.slogan}}"</i></p>
            <p>{{business.address}}<br>{{business.postalCode}}</p>
            <p><span>telf: </span><a :href="'tel:' + business.telf">{{business.telf}}</a></p>
            <p><span>email: </span><a :href="'mailto:' + business.email">{{business.email}}</a></p>
            <p><span>website: </span><a :href="'https://' + business.website">{{business.website}}</a></p>
            <ul v-if="business.socialNetworks" class="socialNetworks">
                <span>red-social:</span>
                <li v-for="socialNetwork in business.socialNetworks" :key="socialNetwork">
                    <a :href="socialNetwork" :title="socialNetwork">
                        <i :class="'fab fa-' + socialNetwork.split('//')[1].split('.')[0]"></i>
                    </a>    
                </li>                
            </ul>
            <p v-if="this.full != i" class="description">
                {{business.description.slice(0,32)}}
                <button @click="toggleDescription(i)">... leer mas</button>
            </p>
            <p v-else class="description">
                {{business.description}}
                <button @click="toggleDescription(i)">reducir</button>
            </p>
            <ul>
                <li v-for="keyword in business.keywords" :key="keyword">{{keyword}}</li>
            </ul>                    
        </article>
        <div class="label"><h5 :class="business.tag">{{business.tag}}</h5></div>
    </div>
</template>

<script>
export default {
    name: "BusinessComp",
    props: {
        businesses: Array
    },
    data() {
        return {
            full: null
        }
    },
    methods: {
        toggleDescription(i) {
            if (this.full != i) {
                return this.full = i    
            }
            return this.full = null
        }
    }
}
</script>

<style lang="scss" scoped>
.business {
    width: fit-content;
    margin: auto;
    margin-bottom: 2rem;
    box-shadow: 0px 0px 14px 0px #00000038;
    padding: 1rem;
    display: flex;
    align-items: flex-start;
    overflow: hidden;
}
.profile-pic {
    overflow: hidden;
    min-width: 120px;
    min-height: 120px;
    max-width: 12px;
    max-height: 120px;
    border-radius: 50%;
    box-shadow: 0px 0px 6px 0px #00000038;
    margin-right: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}
article {
    width: 400px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    h3 {
        font-style: italic;
    }
    .subtitle {
        padding-bottom: 16px;
    }
    p {
        padding: 4px 0;
    }
    span {
        font-weight: 500;
    }
    i {
        padding-right: 24px;
    }
    a {
        font-weight: 300;
        &:hover {
            text-decoration: underline;
        }
    }
    .socialNetworks {
        display: flex;
        span {
            margin-right: 8px;
        }
        li:hover {
            color: #0bd3d3;
        }   
    }
    ul:last-child {
        padding-top: 8px;
        display: flex;
        flex-wrap: wrap;
        font-size: small;
        li:hover {
            background-color: #0bd3d3;
            cursor: pointer;
        }
    }
}
.label {
    width: 140px;
    h5 {
        width: fit-content;
        float: right;
        background-color: #0bd3d3;
    }
}
.description {
    line-height: 1.4;
    opacity: 1;
    font-weight: 300;
    font-style: normal;
    color: #353535;
    button:hover {
        color: #0bd3d3;
    }
}

/* SMARTPHONE DEVICES */
@media screen and (max-width: 992px) {
    .business {
        flex-direction: column;
        width: 100vw; 
    }
    .profile-pic {
        margin-right: 0;
        margin-bottom: 1rem;
    }
    .label {
        position: absolute;
        right: 1rem;
    }
}
</style>