<template>
    <Nav />
    <h2>EMPRESAS_vlc</h2>
    <main>
        <BusinessComp :businesses="this.businesses" />    
    </main>    
    <Footer />
</template>

<script>
import apiUrl from "../../config.js"
import Nav from "../components/Nav.vue"
import BusinessComp from "../components/Business-Comp.vue"
import Footer from "../components/Footer.vue"

export default {
    name: "Business",
    components: {
        Nav,
        BusinessComp,
        Footer
    },
    data() {
        return {
            businesses: []
        }
    },
    methods: {
        async fetchBusinesses() {
            const res = await fetch(`${apiUrl}/api/businesses`)
            const data = await res.json()
            return data
        }
    },
    async created() {
        this.businesses = await this.fetchBusinesses()
    }
}
</script>